<template>
  <div>
    <div class="py-4 px-2 shadow">
        <p class="mt-1 font-semibold text-lg float-right mr-3">GPS Location</p>
        <button @click="$router.go(-1)" type="button"  class="btn btn-sm btn-dark-outline ml-2 rounded-circle px-2" style="border:1px solid black;">
            <span class="typcn typcn-arrow-left"></span>
        </button>
    </div>
    <hr class="my-2">
     <div id="mapid" style="height:50vh;width:100%;background:white;"></div>
     <div>
      <div class="text-center p-3">
        <button type="button" class="btn btn-sm btn-style4 btn-block text-xl mt-4" 
        v-if="loading"
        @click="$router.push(`/absen/selfie?type=${$route.query.type}&target=${$route.query.target}&lat=${lat}&long=${long}`)">Lanjut</button>
      </div>
     </div>
  </div>
</template>
<script>
import axios from 'axios';
import autophp from '@/plugins/auto';
let sdb = new autophp('ci');
var maps;
var watchId;
export default {
  data() {
    return {
      loading:false,
      isInside:false,
      lat:'',
      long:''
    }
  },
  methods: {
    loadMap(){
      let that=this;
       if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition(function (location) {
                appendLocation(location, "fetched");
            });
            watchId = navigator.geolocation.watchPosition(appendLocation);
        } else {
            sdb.alert("Geolocation API not supported.");
        }
      function appendLocation(position, verb) {
        var latitude = position.coords.latitude;
        var longitude = position.coords.longitude;
        var accuracy = position.coords.accuracy;
        console.log('lat',latitude)
        console.log('long',longitude)
        that.lat=latitude
        that.long=longitude
        // var center = JSON.parse(this.$route.query.gps); // 
        maps = new L.Map('mapid');
        maps.setView([latitude,longitude], 18);
        // maps.setView([center.lat,center.long], 18);
        L.tileLayer('http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',{
            maxZoom: 20,
            subdomains:['mt0','mt1','mt2','mt3']
        }).addTo(maps);
        //  L.circle(, 100).addTo(maps);
      
        var user = new L.marker([latitude,longitude],{
        // var user = new L.marker([-3.447172, 114.771320],{
            draggable: false,
            autoPan: true
        }).addTo(maps);
        that.$forceUpdate();
      }
      
     
        
    }
  },
  mounted() {
     setTimeout(() => {
      sdb.alert('Tunggu untuk mendapatkan Pin lokasi anda!','bg-green-400')
    }, 1000);
    setTimeout(() => {
      this.loading=true;
      this.$forceUpdate();
    }, 4000);
    clearInterval(this.$store.state.qrscanInterval);
    setTimeout(() => {
      this.loadMap();
    }, 1000);
    if(localStorage.getItem('users')){
      }else{
          this.$router.push('/')
      }
  }
};
</script>